
h1[data-v-4]{color:#42b983
}
.red[data-v-4]{color:red
}
.list[data-v-4]{width:50%;min-width:300px;font-size:1rem;text-align:left;margin-left:auto;margin-right:auto
}
.list .item[data-v-4]:not(.active){color:gray
}
.list .item:not(.active) a[data-v-4]{color:#aca !important
}
.list .item.active a[data-v-4]{text-decoration:underline !important
}
.list .item input[data-v-4]{margin-right:5px
}
.right.bar[data-v-4]{position:fixed;z-index:9998;top:0;left:0;bottom:0;width:10px;width:1vmax;border-right:1px solid green;background-color:green;background-image:linear-gradient(red, #ff0, green)
}
.right.bar .door[data-v-4]{position:fixed;z-index:9999;top:0;background-color:white;width:10px;width:1vmax;border-top:1px solid green;border-right:1px solid green;border-radius:0 0 3px 3px;transition:bottom 0.5s ease
}
p.choice[data-v-4]{font-size:1rem
}
p.choice input[data-v-4]{margin-left:10px;margin-right:3px;position:relative;top:-1px
}
i.icon[data-v-4],i.icon[data-v-4]::before{text-decoration:none
}
i.chevron.right.icon[data-v-4]{animation:dada 1s ease 3
}
@keyframes dada{
0%{position:relative;left:0
}
50%{position:relative;left:0.25em
}
100%{position:relative;left:0
}
}



